import { useState } from 'react';
import { Helmet } from 'react-helmet';
import { webinarFilter } from '../../../helpers';
import { IWebinarCard } from '../../../interfaces';
import { titlePageGenerator } from '../../../utils';
import Gap from '../../atoms/Gap/Gap';
import Heading2 from '../../atoms/Heading2/Heading2';
import Footer from '../../molecules/Footer/Footer';
import Header from '../../molecules/Header/Header';
import DateFilter from '../../organisms/DateFilter/DateFilter';
import WebinarSeriesListGroup from '../../organisms/WebinarSeriesListGroup/WebinarSeriesListGroup';

function WebinarSeriesPage() {
  const webinarList: IWebinarCard[] = webinarFilter();
  const filterDates = webinarList.map((item) => item.date);
  const availableDates = [...new Set(filterDates)];
  const [activeFilter, setActiveFilter] = useState(availableDates[0] || '');

  return (
    <>
      <Helmet>
        <title>{titlePageGenerator('Webinar Series')}</title>
      </Helmet>
      <Header active="/webinar-series" />
      <main>
        <Gap className="h-6" />
        <Heading2 title="Webinar Series" />
        <DateFilter
          activeFilter={activeFilter}
          setActiveFilter={setActiveFilter}
          availableDates={availableDates}
        />
        <WebinarSeriesListGroup
          activeFilter={activeFilter}
          webinarList={webinarList}
        />
        <Footer isEmployer={false} />
        {/* <EmployerHomeDecorations /> */}
      </main>
    </>
  );
}

export default WebinarSeriesPage;
