import { useState } from 'react';
import { webinarFilter } from '../../../helpers';
import { IWebinarCard } from '../../../interfaces';
import Button from '../../atoms/Button';
import DateEasyRead from '../../atoms/DateEasyRead/DateEasyRead';
import IVFWebinarCard from '../../molecules/IVFWebinarCard/IVFWebinarCard';

function activeButtonGenerator(currentActive: string, date: string) {
  return currentActive === date;
}

interface IWebinarSeriesGroupProps {
  webinarList: IWebinarCard[];
  activeFilter: string;
}

export default function WebinarSeriesListGroup({
  webinarList,
  activeFilter,
}: IWebinarSeriesGroupProps) {
  const filteredFinal = webinarList.filter(
    (item) => item.date === activeFilter
  );

  return (
    <section className="px-6 lg:px-24">
      <div className="mt-3 grid grid-cols-1 gap-4 md:grid-cols-2 lg:grid-cols-4">
        {filteredFinal.map((webinar) => (
          <IVFWebinarCard
            key={webinar.id * Math.random()}
            ctaColor="primary"
            webinarCard={webinar}
          />
        ))}
      </div>
    </section>
  );
}
